export default [	{		path:'/tabber',		name:'tabber',		component:()=>import('@/components/tabbar/TabBar.vue')	},	{		path:'/houses',		name:'houses',		meta:{			title:'',			showTabbar:true		},		component:()=>import('@/views/houses/houses.vue'),	},	{		path:'/houses/details',		name:'housesDetails',		meta:{			title:'',			showTabar:false		},		component:()=>import('@/views/houses/housesDetails.vue')	},	{		path:'/customers',		name:'customers',		component:()=>import('@/views/customers/customers.vue')	},	{		path:'/square',		name:'square',		component:()=>import('@/views/square/square.vue')	},	{		path:'/user',		name:'user',		component:()=>import('@/views/user/user.vue')	},]


