(function(){	const viewWidth=document.documentElement.clientWidth/7.5;	const bodyWidth=document.documentElement.clientWidth;	const $fontSize=viewWidth>=64?64:viewWidth;	const $width=bodyWidth>=414?414:bodyWidth;	const $el=document.documentElement || window.body;	const $options={		margin:'0 auto',		fontSize:$fontSize+'px',		width:$width+'px'	};	setStyle($el,$options)})();function setStyle(el,options){	for(let i in options){		el.style[i]=options[i];	}}


